body{font-family:Arial, Helvetica, sans-serif}.todoswrapper{margin:auto;padding:10px;width:50%;height:100%;background-color:#ddd}.todoswrapper .title{margin:10px 0px}.todoswrapper .addTodoInput{box-sizing:border-box;outline:0;width:100%;padding:10px;background-color:inherit;border-width:0px;border-bottom:2px solid black;transition:0.3s all}.todoswrapper .addTodoInput:focus{background-color:rgba(255,255,255,0.9)}.todoswrapper .todolist{margin-top:10px}.todoswrapper .todolist .item{padding:10px 7px;cursor:pointer}.todoswrapper .todolist .item:hover{background-color:rgba(255,255,255,0.9)}.todoswrapper .todolist .item.COMPLETE{opacity:0.5}.todoswrapper .filters{margin-top:10px;display:flex}.todoswrapper .filters button{padding:7px;flex:1 1}

